.details_label {
	border: 1px solid var(--border-color-2);
	border-radius: 4px;;
	padding: 12px 16px;
	margin: 20px 0 20px 0;
}
.desc {
	padding-top: 16px;
}
.details_value {
	padding: 10px 0;
}

.numbersContainer {
	border: 1px solid var(--border-color-2);
	border-radius: 4px;
	padding: 0 16px;
	margin: 24px 0 50px;
}

.numbersContainer > div {
	padding: 16px 0;
}

.copy {
	min-width: 12px;
}

.walletAddress {
	padding: 20px 16px;
	border-radius: 4px;
	border: 1px solid var(--border-color-2);
	margin-top: 44px;
}

.cryptoAddress {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	flex: 1;
	padding-right: 10px;
}